<template>
    <div class="homeLouPlus">
        <Title :title="'楼+ 实战课程'"
               :description="'定期开班，导师助教全程辅导，最快最优成长'"
        ></Title>

        <div class="louPlusCards">
            <louPlusCard v-for="card in louPlus"
                         :key="card.title"
                         :data="card"
            >
            </louPlusCard>
        </div>
    </div>
</template>
<script type="text/javascript">
import Title from '@/components/common/title.vue'
import louPlusCard from '@/components/common/louPlusCard.vue'
import HomeApi from '@/api/home.js'

export default {
    components: {
        Title,
        louPlusCard
    },
    data() {
        return {
            indexLouPlus: []
        }
    },
    computed: {
        louPlus: function () {
            return this.indexLouPlus.slice(0, 4)
        }
    },
    async created() {
        let res = await HomeApi.getIndexLouplus()
        debugger
        this.indexLouPlus = res.data
    }
}

</script>
