<template>
  <div class="homeMainNav" :style="{background: background}">
    <div class="homeMainNavLayout">
      <div class="homeMainNavLeft" v-if="categories && categories.length !== 0">
        <div v-for="item in categories" :key="item.name" class="homeMainNavLeftItem">
          <div class="homeMainNavLeftItemWrapper">
    <router-link tag="span" :to="{ name: 'courses', query: { category: item.name } }" class="homeMainNavLeftItemSpan homeMainNavLeftItemTitle">
        {{ item.name }}
    </router-link>
    <router-link v-for="subItem in item.tags.slice(0, 2)"
          :key="subItem.name"
          tag="span"
          :to="{ name: 'courses', query: { category: item.name, tag: subItem.name } }"
          class="homeMainNavLeftItemSpan homeMainNavLeftItemSubTitle"

    >
        {{ subItem.name }}
    </router-link>
</div>
          
            <div class="homeMainNavLeftItemSubMenu">
                <span class="homeMainNavLeftItemSubMenuTitle">{{ item.name }}</span>
                <div class="homeMainNavLeftSubMenuContainer">
    <router-link v-for="subMenuItem in item.tags"
          :key="subMenuItem.name"
          tag="span"
          :to="{ name: 'courses', query: { category: item.name, tag: subMenuItem.name } }"
          class="homeMainNavLeftSubMenuContainerItem"
    >
        {{ subMenuItem.name }}
    </router-link>
</div>
                <span class="homeMainNavLeftItemSubMenuTitle">
                    课程推荐
                </span>
                <div class="homeMainNavLeftSubMenuContainerTwo">
                    <span v-for="subMenuItem in item.recommend_courses"
                        class="homeMainNavLeftSubMenuContainerTwoItem">
                        {{ subMenuItem.name }}
                    </span>
                </div>
        </div>
        </div>
        <div class="homeMainNavLeftFooter">
            <a class="homeMainNavLeftFooterItem">经营专区</a>
        </div>
      </div>
      <div class="homeMainNavRight" v-if="banner && banner.length !== 0">
        <transition-group name="bannerTransition" tag="div" class="bannerTransition">
          <img
            v-for="item in banner"
            class="bannerImg"
            v-show="currentBanner === item.picture_url"
            :key="item.picture_url"
            :src="item.picture_url"
          />
        </transition-group>
      </div>
      
    </div>
  </div>
</template>

<script type="text/javascript">
import HomeApi from "@/api/home.js";

export default {
  data() {
    return {
      categories: {},
      banner: [],
      currentBanner: '',
      currentIndex: 0,
      background: ''
    };
  },

  async created() {
    // 这是异步请求的语法糖，你还可以用 then 来写。
    let res = await HomeApi.getIndexCategories();

    // 这里你可以在 api/base.js 的自定义里添加拦截器来判断请求是否正确返回，这里先全按正确返回进行处理。

    this.categories = res.data;
    console.log(this.categories)

    // 这个断点仅仅为了查看我们刚才请求到的信息，你可以把它去掉。大家使用以后尽量去掉，避免以后启动失败。
    debugger;

    let resTwo = await HomeApi.getIndexBanner();

    this.banner = resTwo.data;
    this.currentBanner = resTwo.data[0].picture_url
        setInterval(() => {
            if (this.currentIndex+1 === this.banner.length) {
                this.currentIndex = 0
                this.currentBanner = this.banner[this.currentIndex].picture_url
                return
            }

            this.currentIndex += 1
            this.currentBanner = this.banner[this.currentIndex].picture_url

        }, 2000)
    this.background = resTwo.data[0].background_color

        setInterval(() => {
            if (this.currentIndex+1 === this.banner.length) {
                this.background = this.banner[this.currentIndex].background_color
                return
            }

            this.background = this.banner[this.currentIndex].background_color

        }, 2000)
  }
};
</script>
