<template>
    <div class="pathCards">
        <Title :title="'学习路径'"
               :description="'精准而系统的学习路径，从 0 到大牛'"
        ></Title>

        <div class="pathCardsLayout">
            <PathCard v-for="cls in pathCards"
                          :key="cls.id"
                          :data="cls"
                          class="pathCard"
            >
            </PathCard>
        </div>
    </div>
</template>

<script type="text/javascript">
import Title from '@/components/common/title.vue'
import PathCard from '@/components/common/pathCard.vue'
import HomeApi from '@/api/home.js'

export default {
    components: {
        Title,
        PathCard
    },
    data() {
        return {
            pathCards: []
        }
    },
    async created() {
        let res = await HomeApi.getPaths()
        debugger
        this.pathCards = res.data
    }
}
</script>
