<template>
    <div class="classesDiv">
        <div class="classes" v-for="cls in classify"
                             :key="cls.classify_name"
        >
            <Title :title="cls.classify_name" :description="cls.description">
            </Title>

            <div v-if="cls.classify_name !== '近期好课'" class="classesItemDiv" :class="[Object.keys(cls.recommend_course).length !== 0 ? (cls.recommend_course.layout_type==='horizontal' ? 'horizontalClassesItemDiv' : 'verticalClassesItemDiv' ) : ''  ]">
                <div v-if="Object.keys(cls.recommend_course).length !== 0" class="recommendCourseDiv">
                    <img class="recommendCourseImg" :src="cls.recommend_course.picture_url">
                </div>

                <CourseCard v-for="course in cls.courses"
                            :key="course.id"
                            :data="course"
                            class="courseCard"
                >

                </CourseCard>
            </div>
            <div v-else class="classesScrollItemDiv">
                <transition-group class="classesScrollItemTransitionLayout" tag="div" :name="currentTurn === 'right' ? 'rightTransition' : 'leftTransition'">
                    <div class="classesScrollItemLayout" v-for="index in 3"
                                                      :key="index"
                                                      v-show="index == currentIndex"
                    >
                        <CourseCard v-for="indexTwo in 4"
                                    :key="`${index}${indexTwo}`"
                                    :data="cls.courses[indexTwo-1+(4*(index-1))]"
                                    class="courseCard"
                        >
                        </CourseCard>
                    </div>
                </transition-group>

                <div class="tabButton tabButtonLeft" @click="turnLeft"></div>
                <div class="tabButton tabButtonRight" @click="turnRight"></div>
                <div class="shadowDiv"
                     :class="hasTurn? 'transitionShadowDivLeft':''">
                </div>
                <div class="shadowDivTwo"
                     :class="hasTurn? 'transitionShadowDivRight':''">

                </div>
            </div>
        </div>
    </div>
</template>

<script type="text/javascript">
import Title from '@/components/common/title.vue'
import CourseCard from '@/components/common/courseCard.vue'
import HomeApi from '@/api/home.js'

export default {
    components: {
        Title,
        CourseCard
    },
    data() {
        return {
            classify: [],
            currentIndex: 1,
            currentTurn: 'right',
            hasTurn: false
        }
    },
    methods: {
        turnLeft: function () {
            this.currentTurn = 'left'
            this.hasTurn = true
            if (this.currentIndex - 1 >= 1) {
                this.currentIndex -= 1
            } else {
                this.currentIndex = 3
            }
            setTimeout(() => { this.hasTurn = false }, 600)
        },

        turnRight: function () {
            this.currentTurn = 'right'
            this.hasTurn = true
            if (this.currentIndex + 1 > 3) {
                this.currentIndex = 1
            } else {
                this.currentIndex += 1
            }
            setTimeout(() => { this.hasTurn = false }, 600)
        }
    },
    async created() {
        let res = await HomeApi.getClassficationCourses()
        debugger
        this.classify = res.data
    }
}
</script>
<style type="text/css" scoped>
.classesScrollItemDiv {
  display: flex;
  position: relative;
}

.classesScrollItemTransitionLayout {
  width: 100%;
  height: 270px;
  overflow: hidden;
  position: relative;
}

.classesScrollItemLayout {
  display: flex;
  width: 100%;
  position: absolute;
}

.classesScrollItemDiv:hover .tabButton {
  display: block;
}

.tabButton {
  position: absolute;
  top: 50%;
  height: 60px;
  width: 60px;
  transform: translateY(-50%);
  border-radius: 30px;
  background: #9e9c9c;
  opacity: 0.6;
  cursor: pointer;
  display: none;
}

.tabButtonLeft {
  left: 15px;
}

.tabButtonRight {
  right: 15px;
}

.tabButtonLeft::before,
.tabButtonRight::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 30px;
  color: #fff;
}

.tabButtonLeft::before {
  content: "<";
}

.tabButtonRight::before {
  content: ">";
}

.rightTransition-enter-to,
.rightTransition-leave,
.leftTransition-enter-to,
.leftTransition-leave {
  transition: all 0.6s ease;
  transform: translateX(0);
}

.rightTransition-enter,
.leftTransition-leave-to {
  transition: all 0.6s ease;
  transform: translateX(-100%);
}

.rightTransition-leave-to,
.leftTransition-enter {
  transition: all 0.6s ease;
  transform: translateX(100%);
}

.shadowDiv,
.shadowDivTwo {
  position: absolute;
  width: 20px;
  height: 250px;
  opacity: 0.3;
  transition: all 0.3s ease-in-out;
}

.shadowDiv {
  left: 15px;
  background-image: linear-gradient(270deg, transparent, #777);
}

.shadowDivTwo {
  right: 15px;
  background-image: linear-gradient(90deg, transparent, #777);
}

.transitionShadowDivLeft {
  left: 0;
}

.transitionShadowDivRight {
  right: 0;
}
.classesDiv {
    width: 1140px;
    margin: auto;
    display: flex;
    flex-direction: column;
}

.classesItemDiv {
    display: flex;
    flex-wrap: wrap;
}

.horizontalClassesItemDiv .recommendCourseDiv {
    width: 50%;
}

.horizontalClassesItemDiv .recommendCourseImg {
    width: 100%;
    padding: 0 15px;
}

.verticalClassesItemDiv {
    flex-direction: column;
    height: 540px;
    max-height: 540px;
}

.verticalClassesItemDiv .recommendCourseDiv {
    width: 25%;
    height: 100%;
}

.verticalClassesItemDiv .recommendCourseImg {
    width: 100%;
    padding: 0 15px;
    padding-bottom: 20px;
    height: 100%;
}

.courseCard {
    width: 25%;
    padding: 0 15px;
    padding-bottom: 20px;
}
</style>