<template>
    <div class="titleDescriptionDiv">
        <div class="title">
            {{ title }}
        </div>
        <div class="description" v-if="description">
            <span class="separator">|</span>
            {{ description }}
        </div>
    </div>
</template>

<script type="text/javascript">
export default {
    props: {
        title: {
            type: String,
            require: true
        },
        description: {
            type: String,
            require: false,
            default: ''
        }
    }
}
</script>
<style type="text/css" scoped>

.titleDescriptionDiv {
    display: flex;
    align-items: center;
    margin: 30px 0;
}

.title {
    color: #565656;
    font-size: 24px;
}

.description {
    color: #666;
    font-size: 14px;
}

</style>