<template>
    <transition name="loginBackgroundTransition">
        <div class="loginDiv"
             v-show="visible"
        >
            <transition name="loginDialogTransition">
                <div class="loginDialogDiv"
                     v-show="visible"
                >
                    <div class="navBar">
                        <div class="closeDialogDiv">
                            <span class="closeDialogSpan"
                             @click="close"
                            >×</span>
                        </div>
                        <ul class="navBarUl">
                            <li class="navBarLi"
                            >
                                <a href="javascript:;"
                                   class="navBarA"
                                >登录</a>
                            </li>
                            <li class="navBarLi"
                            >
                                <a href="javascript:;"
                                   class="navBarA"
                                >注册</a>
                            </li>
                        </ul>
                    </div>
                    <div class="inputGroup">
                        <input class="inputForm" type="text" name="" placeholder="请输入手机/邮箱" v-model="username">
                        <input class="inputForm" type="password" name="" placeholder="请输入密码" v-model="password">
                        <a class="enterButton"
                           href="javascript:;"
                           @click="login"
                        >进入实验楼</a>
                    </div>
                </div>
            </transition>
        </div>
    </transition>
</template>

<script type="text/javascript">
import cookies from 'vue-cookies'
import { mapActions } from 'vuex'
export default {
    props: {
        visible: {
            required: true,
            type: Boolean
        }
    },
    data() {
        return {
            username: '',
            password: ''
        }
    },
    methods: {
        ...mapActions({
            changeLoginState: 'login/changeLoginState'
        }),
        async login() {
            let res = await loginApi.login({
                login: this.username,
                password: this.password,
                remeber: false
            })
            if (res.data.code !== 'validation_error') {
                this.changeLoginState(true)
                cookies.set('session', res.data.session)
                let resTwo = await userApi.getUserInfo()
                this.changeUserInfo(resTwo.data)
                this.close()

            }
        },
        close() {
            this.$emit('update:visible', false)
        },
        login() {

        }
    }
}
</script>
<style type="text/css" scoped>
.loginDiv {
    height: 100%;
    width: 100%;
    position: fixed;
    background: rgb(0,0,0,0.5);
    z-index: 99;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

.loginDialogDiv {
    opacity: 1;
    width: 350px;
    margin: 80px auto;
    margin-top: 50px;
    background: #fff;
    padding: 10px 20px;
    border-radius: 4px;
    border: 1px solid rgba(0,0,0,0.2);
}

/* 登录背景会淡出 */

.loginBackgroundTransition-enter-active, .loginBackgroundTransition-leave-active {
    transition: all .4s ease-out;
}

.loginBackgroundTransition-enter-to, .loginBackgroundTransition-leave {
    opacity: 1;
}

.loginBackgroundTransition-leave-to, .loginBackgroundTransition-enter {
    opacity: 0;
}

.loginDialogTransition-enter-active, .loginDialogTransition-leave-active {
    transition: all .4s ease-out;
}

.loginDialogTransition-enter, .loginDialogTransition-leave-to {
    opacity: 0;
}

.loginDialogTransition-enter-to, .loginDialogTransition-leave {
    opacity: 1;
}

.closeDialogDiv {
    text-align: right;
    color: #000;
    opacity: .2;
    font-size: 21px;
    font-weight: 700;
    text-shadow: 0 1px 0 #fff;
    cursor: pointer;
    height: 20px;
}

.navBarUl {
    display: flex;
    justify-content: space-around;
    border: none;
    border-bottom: 1px solid #eee;
    margin-bottom: 10px;
}

.navBarLi {
    width: 50%;
    text-align: center;
}

.navBarLi:hover {
    border-bottom: 1px solid #0c9;
}

.navBarA {
    display: block;
    font-weight: 700;
    padding: 15px;
    font-size: 18px;
    color: #555;
}

.navBarLiActive {
    border-bottom: 1px solid #0c9;
}

.inputGroup {
    display: flex;
    flex-direction: column;
}

.inputForm {
    display: block;
    width: 100%;
    margin-bottom: 15px;
    height: calc(1.5em + .75rem + 2px);
    padding: .375rem .75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.inputForm::placeholder {
    color: #c6c6c6;
}

.inputForm:focus {
    box-shadow: none;
    outline: none;
    border-color: #08bf91;
}

.enterButton {
    display: block;
    width: 100%;
    color: #fff;
    background: #0c9;
    padding: 6px 12px;
    font-size: 14px;
    text-align: center;
    border: 1px solid #0c9;
    border-radius: 20px;
}

.enterButton:hover {
    background: #007558;
    color: #fff;
}
</style>